import { createRouter, createWebHistory } from 'vue-router'
import Login from '../views/Auth/Login.vue'
import Warehousman from '../views/Warehousman/Warehousman.vue'
import Director from '../views/Director/Director.vue'
import Financier from '../views/Financier/Financier.vue'
import Clients from '../views/Clients/Clients.vue'
import User from '../views/Global/User.vue'
import Products from '../views/Global/Products.vue'
import Singlepage from '../views/Global/Singlepage.vue'
const routes = [
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path:"/product-list",
    name:'product-list',
    component:Warehousman,
    children:[
      {
        indexedDB,
        path:'/warehouse_user',
        name:'warehouse_user',
        component:User
      },
      {
        path:'/products',
        name:'/prodcuts',
        component:Products
      },
      {
        path:'/singlepage_product/:id',
        name:'singlepage_product',
        component:Singlepage
      }
    ]
  },
  {
    path:'/director',
    name:'director',
    component:Director ,
    children :[
      {
        path:'/director_user',
        name:'director_user',
        component:User
      }
    ]
  },
  {
    path:'/clients',
    name:'clients',
    component:Clients ,
    children :[
      {
        path:'/clients_user',
        name :'clients_user',
        component :User
      }
    ]
    
  },
  {
    path:'/financier',
    name:'financier',
    component:Financier , 
    children :[
      {
        path:'/financier_user',
        name :'financier_user',
        component:User
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to, from , next) =>{
  const token = sessionStorage.getItem('token')
  const role = sessionStorage.getItem('role')
  const name = to.name === 'login'
  if(!token && !name){
    return next({name:'login'})
  }else{
    if(token && name){
      if(role === 'warehouseman'){
        return next({name:"product-list"})
      }
      else if(role === 'salesman'){
        return next({name:"clients"})
      }
      else if(role === 'director'){
        return next({name:"director"})
      }
      else if(role === 'financier'){
        return next({name:'financier'})
      }
    }
    else{
      next()
    }
  }
})

export default router
