<template>
    <div class="product__lists router" >
        <div class="product__lists-action">
            <router-link to="/groups-brands" class="btn" >
                <span >Groups and Brands</span>
                <i class="right fa-solid fa-right-long" ></i>
            </router-link>
            <button class="btn">Add Product</button>
        </div>
        <div class="product__lists-table" >
           <appTable :headers="headers" :body="store.state.products">
               <template #body_group="{item}">
                <span>{{ item.group.title }} </span>
               </template>
               <template #body_brand="{item}">
                <span>{{ item.brand.title }} </span>
               </template>
               <template #body_title="{item}">
                 <span class="product__lists-table-text">{{ item.title}} </span>
               </template>
               <template #body_description="{item}">
                 <span class="product__lists-table-text">{{ item.description}} </span>
               </template>
               <template #body_action="{item}">
                <span>
                  <router-link :to="{name:'singlepage_product' , params:{id:item.id} }" class="single__route-icon">
                    <i class="fa-solid fa-angles-right"></i>
                  </router-link>
                </span>
               </template>
                
           </appTable>
        </div>   
</div>
</template>

<script setup>
import {ref ,onMounted} from 'vue'
import appTable from '@/components/ui/app-table.vue';
import store from '@/store'
const headers = ref([
 {title:"" , value:'index'},
  {title: 'Name', value:'title'},
  {title: 'Code', value:'code'},
  {title: 'Brand', value:'brand'},
  {title: 'Group', value:'group'},
  {title: 'Arrival price', value:'current_arrival_price'},
  {title: 'Selling price', value:'current_selling_price'},
  {title: 'Description', value:'description'},
  {title: 'Action', value:'action'},
])

onMounted(() => {
   store.dispatch('getProducts')
 })
  
</script>

<style lang="scss" scoped>
   .product__lists-action{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 15px;
    gap: 15px;
    .btn{
        padding: 10px 15px;
    border: none;
    border-radius: 5px;
    background-color: #435ebe;
    color: #fff;
    cursor: pointer;
    display: flex;
    gap: 8px;
    align-items: center;
    }
  }
  .product__lists-table-text{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    color: #000;
  }
  .single__route-icon{
    color: #435ebe;
  }
</style>